<template>
	<div id="news">
		<div class="All">
			<h3><span class="sp">文章列表</span></h3>
			<ul v-if="articleList.length">
				<RouterLink v-for="item in articleList" :key="item.id" :to="'/home/article?id='+item.id" tag="li">
					<div class="l">
						<img :src="item.img" alt="图片">
					</div>
					<div class="r">
						<h2>{{item.title}}</h2>
						<div class="info">
							<img :src="item.avator" alt="头像" class="avator">
							<h4>
								<span>作者:{{item.username}}</span><span>时间:{{item.ctime | dateFormate }}</span>
							</h4>
						</div>
						<p v-html="item.sintro.trim()"></p>
					</div>
				</RouterLink>
			</ul>
			<h3 class="tips" v-if="!articleList.length">暂时没有文章</h3>
		</div>
	</div>
</template>

<script>
	export default{
		data(){
			return {
				articleList:null,
				page:1
			}
		},
		methods:{
			getArticleList(){
				let query = this.$route.query
				if(query.classifyid){
					this.$http.get('/article/queryArticle',{params:{classifyId:query.classifyid,page:this.page}}).then(data=>{
						this.articleList = data.body
					})
					return
				}
				if(query.keywords){
					this.$http.get('/article/searchArticle',{params:{keyword:query.keywords}}).then(data=>{
						this.articleList = data.body
					})
				}
			}
		},
		created(){
			this.getArticleList()
		},
		watch:{
			$route:function(){
				this.getArticleList()
			}
		}
	}
</script>

<style lang="less" scoped="scoped" rel="stylesheet/less">
	.All {
		margin-bottom: 15px;
		padding-left: 25px;
		width: 1000px;
		font-size: 16px;
		background-color: #fff;
		.sp{
			height: 45px;
			font-weight: 700;
			line-height: 45px;
			border-bottom: 3px solid cornflowerblue;
			transition: all 0.5s ease;
			display: block;
			width:65px;
			cursor: pointer;
			&:hover{
				width: 80px;
			}
		}
		h3.tips{
			height: 35px;
			text-align: center;
			line-height: 35px;
		}
		ul {
			padding-left: 25px;
			padding-right: 50px;
	
			li {
				height: 190px;
				padding: 10px 0;
				overflow: hidden;
				cursor: pointer;
	
				.l {
					width: 280px;
					margin-right: 20px;
	
				}
	
				.r {
					.info {
						display: flex;
						justify-content: space-between;
	
						h4 {
							font-size: 14px;
							padding-left: 15px;
							width: 550px;
							display: flex;
							justify-content: space-between;
							flex-direction: column;
						}
					}
	
					width:600px;
	
					p {
						line-height: 30px;
						overflow: hidden;
						display: -webkit-box;
						-webkit-line-clamp: 3;
						-webkit-box-orient: vertical;
						text-overflow: ellipsis;
						margin-top: 10px;
					}
	
					h2 {
						font-weight: 700;
						margin-bottom: 5px;
					}
	
					img {
						width: 50px;
						height: 50px;
						border-radius: 50%;
						display: inline-block;
						vertical-align: center;
					}
				}
	
				display:flex;
				justify-content: space-between;
	
				img {
					display: block;
					height: 170px;
					width: 269px;
				}
			}
		}
	}
</style>



