<template>
	<div id="article">
		<h3>{{article.title}}<b><span :class="{'iconfont':true,'orange':flag===true}" @click="collect">&#xe60a;</span>:{{collctLength}}</b></h3>
		<div class="info">
			<img :src="article.avator" alt="avator">
			<div>
				<p class="user"><span>作者:{{article.username}}</span><span>发表于:{{article.ctime | dateFormate}}</span></p>
				<p>分类:{{article.classify}}</p>
			</div>
		</div>
		<section class="text">
			<p class="sintro"><span>简介:</span>{{article.sintro}}</p>
			<section v-html="article.content"></section>
		</section>
		<comment :articleId="id"></comment>
	</div>
	
</template>

<script>
	import comment from './comments.vue'
	export default {
		data() {
			return {
				article:null,
				articleId:this.$route.query.id,
				id:localStorage.getItem('id'),
				collectId:'',
				collctLength:0,
				flag:false
			}
		},
		components:{
			comment
		},
		methods:{
			collect(){
				this.$http.get('/user/queryUser',{params:{id:this.id}}).then(data=>{
					if(data.body.message=='未登录'){
						return this.$message('您还没有登录哦')
					}
					let articleId = this.$route.query.id
					if(this.flag){
						this.$http.get('/collect/cancelCollect',{params:{id:this.collectId}}).then(data=>{
							if(data.body.success){
								this.flag=false
								this.$message('取消收藏成功')
								this.collctLength--
							}
						})
					}else{
						this.$http.get('/collect/addCollect',{params:{userId:this.id,articleId:articleId}}).then(data=>{
							if(data.body.success){
								this.flag = true
								this.$message('收藏成功')
								this.collctLength++
							}
						})
					}
				})
			},
			getCollectLength(){
				let id = this.$route.query.id
				this.$http.get('/collect/collectLength',{params:{articleId:id}}).then(data=>{
					this.collctLength = data.body[0].count
				})
			},
			isCollect(){
				this.$http.get('/user/queryUser',{params:{id:this.id}}).then(data=>{
					if(data.body.message=='未登录'){
						return
					}
					let articleId = this.$route.query.id 
					this.$http.get('/collect/isCollect',{params:{articleId:articleId,userId:this.id}}).then(data=>{
						console.log(data)
						if(data.body.length){
							this.collectId = data.body[0].id
							return this.flag = true
						}
						this.flag = false
					})
				})
			},
			getArticle(){
				let id = this.$route.query.id
				this.$http.get('/article/info',{params:{id:id}}).then(data=>{
					this.article = data.body[0]
				})
			}
		},
		created(){
			this.getArticle()
			this.getCollectLength()
			this.isCollect()
		},
		watch:{
			$route(){
				this.getArticle()
				this.getCollectLength()
				this.isCollect()
			}
		}
	}
</script>

<style lang="less" rel="stylesheet/less">
	#article{
		img {
			max-width: 100%;
		}
	}
</style>
<style lang="less" scoped="scoped" rel="stylesheet/less">
	#article{
		background-color: #FFFFFF;
		padding: 15px;
		margin-bottom: 15px;
		overflow: hidden;
		height: auto;
		.orange{
			color: orangered;
		}
		.iconfont{
			cursor: pointer;
		}
		.info{
			display: flex;
			justify-content: space-between;
			img{
				width: 45px;
				height: 45px ;
				border-radius: 50%;
				display: block;
				margin: 2px 0;
			}
			div{
				width: 940px;
				font-size: 14px;
				p{
					height: 25px;
					line-height: 25px;
					&.user{
						display: flex;
						justify-content: space-between;
					}
				}
			}
		}
		h3{
			font-size: 18px;
			font-weight: 700;
			text-align: center;
			margin: 15px 0;
			display: flex;
			justify-content: space-between;
			b{
				font-weight: normal;
				margin-left: 100px;
			}
		}
		.text{
			.sintro{
				line-height:30px;
				background-color:rgba(0,0,0,.2);
				padding:15px 5px ;
				span{
					font-weight: 700;
					display: inline-block;
					margin-right: 10px;
				}
				section{
					line-height: 25px;
					p{
						margin-bottom: 10px;
					}
				}
			}
		}
	}
</style>



