<template>
	<div id="app-container">
		<hea></hea>
		<transition>
			<router-view v-if="isRouterAlive" class = "view"></router-view>
		</transition>
		<foot v-if="flag"></foot>
	</div>
</template>

<script>
	import hea from './component/header.vue'
	import foot from './component/foot.vue'
	export default {
		data() {
			return {
				 isRouterAlive:true,
				 flag:true
			}
		},
		 provide(){
			return {
				reload:this.reload
			}
		},
		methods:{
			reload(){
				this.isRouterAlive = false
				//在修改数据之后使用 $nextTick，则可以在回调中获取更新后的 DOM
				this.$nextTick(()=>{
				this.isRouterAlive = true
				})
			}
		},
		components: {
			hea,foot
		},
		watch:{
			$route:function (){
				if (this.$route.path=='/login' || this.$route.path=='/writeArticle') {
					this.flag = false
				}else{
					this.flag = true
				}
			}
		}
	}
</script>

<style lang="less" rel="stylesheet/less">
	body{
		position: relative;
		top: 0;
		left: 0;
		bottom: 0;
		right: 0;
		background-color: #E0E0E0;
	}
	#app-container {
		position: absolute;
		top: 0;
		left: 0;
		bottom: 0;
		right: 0;
		padding-top: 60px;
		.view{
			width: 1290px;
			min-height: 100%;
			margin: 0 auto;
			// background-color: pink;
		}
	}
	.v-enter {
		opacity: 0;
		transform: translateX(100%);
	}
	
	.v-leave-to {
		opacity: 0;
		transform: translateX(-100%);
	}
	
	.v-enter-active,
	.v-leave-acive {
		transition: all 0.3s linear;
	}
</style>


