<template>
	<div class="left">
		<!-- 轮播 -->
		<el-carousel :interval="4000" type="card" height="200px">
			<el-carousel-item v-for="item in swipe" :key="item.id">
				<router-link :to="'/home/article?id='+item.id">
					<img :src="item.src" alt="swipe">
				</router-link>
				</h3>
			</el-carousel-item>
		</el-carousel>
		<div class="special">
			<h3><span class="sp">特别推荐</span></h3>
			<ul>
				<router-link v-for="item in special" :to="'/home/article?id='+item.id" tag="li" :key="item.id">
					<span class="title">{{item.title}}</span>
					<img :src="item.img" :alt="item.title">
					<p v-html="item.sintro.substr(0,150).trim()">
					</p>
				</router-link>
			</ul>
		</div>
		<div class="All">
			<h3><span class="sp">所有文章</span></h3>
			<ul>
				<RouterLink v-for="item in all" :key="item.id" :to="'/home/article?id='+item.id" tag="li">
					<div class="l">
						<img :src="item.img" alt="图片">
					</div>
					<div class="r">
						<h2>{{item.title}}</h2>
						<div class="info">
							<img :src="item.avator" alt="头像" class="avator">
							<h4>
								<span>作者:{{item.username}}</span><span>时间:{{item.ctime | dateFormate }}</span>
							</h4>
						</div>
						<p v-html="item.sintro.trim()"></p>
					</div>
				</RouterLink>
			</ul>
			<a href="javascript:;" @click="getMore" v-html="more" class="more"></a>
		</div>
	</div>
</template>

<script>
	export default {
			data(){
			return {
				swipe:[{
					src:'/static/img/1.jpg',
					id:26
				},{
					src:'/static/img/2.jpg',
					id:27
				},{
					src:'/static/img/3.jpg',
					id:28
				},{
					src:'/static/img/4.jpg',
					id:29
				}],
				special:null,
				all:[],
				count:0,
				page:1,
				more:'加载更多'
			}
		},
		methods:{
			getMore(){
				if( this.page < Math.ceil(this.count/10)){
					this.page++
					console.log(this.page)
					this.getAll()
				}else{
					return this.more = '没有更多数据了'
				}
			},
			getArticleLength(){
				this.$http.get('article/articleLength').then(data=>{
					this.page = data.body[0].count
				})
			},
			getAll(){
				this.$http.get('/article/queryArticle',{params:{page:this.page}}).then(data=>{
					this.all = this.all.concat(data.body)
				})
			},
			getArticleCount(){
				this.$http.get('/article/articleLength').then(data=>{
					let count = data.body[0].count
					this.count = count
					this.$store.commit('updateArticleCount',count)
				})
			},
			getspecial(){
				this.$http.get('/article/queryArticle',{params:{length:9}}).then(data=>{
					// console.log(data)
					this.special = data.body
				})	
			}
		},
		created(){
			this.getspecial()
			this.getArticleCount()
			this.getAll()
		}
	}
</script>
<style lang="less" rel="stylesheet/less">
	.el-carousel__item img {
		display: block;
		margin: 0 auto;
		width: 100%;
		height: 100%;
	}

	.el-carousel {
		width: 1000px;
		padding-top: 40px;
		display: inline-block;
		height: 280px;
		vertical-align: top;
		background-color: rgba(0, 0, 0, .1)
	}

	#home .el-carousel__button {
		background-color: cornflowerblue;
	}
</style>
<style lang="less" scoped="scoped" rel="stylesheet/less">
	.left {
		width: 100px;
		.more {
			display: block;
			color: cornflowerblue;
			text-align: center;
			height: 45px;
			line-height: 45px;
		}

		h3 {
			height: 45px;
			line-height: 45px;
			padding-left: 25px;

			span {
				display: block;
				font-weight: 700;
				height: 40px;
				width: 65px;
				border-bottom: 3px solid cornflowerblue;
				transition: all 0.5s ease;
				cursor: pointer;

				&:hover {
					width: 80px;
				}
			}
		}
		.All {
			margin-bottom: 15px;
			padding-left: 25px;
			width: 1000px;
			font-size: 16px;
			background-color: #fff;

			ul {
				padding-left: 25px;
				padding-right: 50px;

				li {
					height: 190px;
					padding: 10px 0;
					overflow: hidden;
					cursor: pointer;

					.l {
						width: 280px;
						margin-right: 20px;

					}

					.r {
						.info {
							display: flex;
							justify-content: space-between;

							h4 {
								font-size: 14px;
								padding-left: 15px;
								width: 550px;
								display: flex;
								justify-content: space-between;
								flex-direction: column;
							}
						}

						width:600px;

						p {
							line-height: 30px;
							overflow: hidden;
							display: -webkit-box;
							-webkit-line-clamp: 3;
							-webkit-box-orient: vertical;
							text-overflow: ellipsis;
							margin-top: 10px;
						}

						h2 {
							font-weight: 700;
							margin-bottom: 5px;
						}

						img {
							width: 50px;
							height: 50px;
							border-radius: 50%;
							display: inline-block;
							vertical-align: center;
						}
					}

					display:flex;
					justify-content: space-between;

					img {
						display: block;
						height: 170px;
						width: 269px;
					}
				}
			}
		}
	}
	.order,
	.special {
		background-color: #fff;
		margin-top: 15px;
		font-size: 16px;
		display: inline-block;
		vertical-align: top;

		h3 {
			height: 45px;
			line-height: 45px;
			padding-left: 25px;

			span {
				display: block;
				font-weight: 700;
				height: 40px;
				width: 65px;
				border-bottom: 3px solid cornflowerblue;
				transition: all 0.5s ease;
				cursor: pointer;

				&:hover {
					width: 80px;
				}
			}
		}
	}

	.special {
		width: 1000px;
		padding: 15px 20px;
		margin-bottom: 15px;
		font-size: 0;

		.sp {
			font-size: 16px;
		}

		ul {
			width: 960px;
			overflow: hidden;
			height: 989px;
			overflow: hidden;

			li {
				cursor: pointer;
				display: inline-block;
				width: 320px;
				height: 330px;
				border-right: 1px solid #ccc;
				border-bottom: 1px solid #ccc;
				overflow: hidden;
				padding: 10px 25px;
				font-size: 16px;

				&:nth-child(3n) {
					border-right: none;
				}

				&:hover {
					border: none;
					box-shadow: 2px 2px 5px rgba(0, 0, 0, .3);

					img {
						transform: scale(1.1);
					}
				}

				span {
					display: inline-block;
					font-weight: 600;
					color: cornflowerblue;
					height: 40px;
					line-height: 30px;
					overflow: hidden;
					white-space: nowrap;
					text-overflow: ellipsis;
					width: 100%;
				}

				p {
					line-height: 31px;
					overflow: hidden !important;
					display: -webkit-box;
					-webkit-line-clamp: 3;
					-webkit-box-orient: vertical;
					// white-space: nowrap;
					text-overflow: ellipsis;
					margin-top: 10px;
				}

				img {
					display: block;
					width: 100%;
					height: 170px;
					transition: all 0.5s ease;
				}
			}
		}
	}
</style>



