<template>
	<div id="reply">
		<ul class="replyList" v-if="reply.length">
			<li v-for="(item,i) in reply" :key="item.id" @click="eply(i)">
				<span class="author">{{item.author}}</span><span v-if="item.replyuser" class="author">&nbsp;回复&nbsp;{{item.replyuser}}</span>&nbsp;:&nbsp;{{item.content}}
			</li>
		</ul>
		<div class="input" v-if="flag">
			<el-input :placeholder="tips" v-model="text" :max="2"></el-input>
			<el-button type="primary" @click="addReply">发表</el-button>
			<el-button type="danger" @click="cancel">取消</el-button>
		</div>
	</div>
</template>

<script>
	export default {
		props:['commentid'],
		data() {
			return {
				flag:false,
				comment:this.commentid,
				reply:[],
				text:'',
				replyuser:'',
				tips:'',
				info:null,
				client:''
			}
		},
		methods:{
			getUser(){
				
			},
			getReply(){
				this.$http.get('/reply/queryReply',{params:{commentId:this.comment}}).then(data=>{
					this.reply = data.body
				})
			},
			cancel(){
				this.flag = false
			},
			eply(i){ 
				this.tips = '回复' + this.reply[i].author
				this.flag = true
				this.info = this.reply[i]
			},
			addReply(){
				let userId = localStorage.getItem('id') ? localStorage.getItem('id') : 1
				this.$http.get('/user/queryUser',{params:{id:userId}}).then(data=>{
					if(data.body.message=='未登录'){
						 return this.$message({
							message: '登录后才能评论哦',
							type: 'error'
						})
					}
					let author = data.body[0].username
					if(!this.text){
						return  this.$message({
							message: '您还没有输入任何内容哦！',
							type: 'error'
						})
					}
					let commentId = this.info.commentid
					this.$http.post('/reply/addReply',{userId:userId,commentId:commentId,content:this.text,replyUser:this.info.author}).then(data=>{
					if(data.body.success){
						this.$message({
							message: '回复成功',
							type: 'success'
						})
						this.reply.push({
							author:author,
							commentid: commentId,
							content: this.text,
							replyuser: this.info.author
						})
						this.flag = false
					 }else{
						this.$message({
							message: '回复失败',
							type: 'error'
						}) 
					 }
				 })
			 })
			}
		},
		created(){
			this.getReply()
		},
		watch:{
			$route(){
				this.getReply()
			}
		}
	}
</script>

<style lang="less" rel="stylesheet/less">
	#reply{
		.input{
			input,button{
				display: inline-block;
			}
			.el-input__inner{
				width: 750px;
				display: inline-block;
			}
		}
	}
</style>

<style lang="less" scoped="scoped" rel="stylesheet/less">
	#reply{
		.replyList{
			background-color: #ddd;
			height: auto;
			li{
				cursor: pointer;
				font-size: 14px;
				// height: 25px;
				line-height: 25px;
				padding: 15px 15px 0;
				.author{
					color: cornflowerblue;
				}
			}
		}
		.input{
			display: flex;
			justify-content: flex-start;
			padding: 0 15px 15px;
			transition: all 0.5s ease;
		}
	}
</style>



