<template>
	<div id="editor">
	</div>
</template>

<script>
	import Editor from 'wangeditor'
	import 'wangeditor/release/wangEditor.min.css'
	export default{
		props:['flag'],
		data(){
			return {
				editorContent:''
			}
		},
		methods:{
			listenUp(editor){
				editor.customConfig.uploadImgHooks = {
					success(xhr, editor, result){
						console.log(123)
					},
					error(xhr, editor){
						console.log(arguments)
					}
				}
			}
		},
		mounted(){
			this.editor = new Editor("#editor")
			this.editor.customConfig = {
				onchange : (html) =>{
					this.editorContent = html
					this.$emit('html',html)
				},
				uploadImgServer : '/article/uploadImg',
				uploadImgMaxSize : 3 * 1024 * 1024,
				uploadImgMaxLength : 5,
				withCredentials : true,
				uploadFileName:'uploadImg',
				uploadImgTimeout:3000
			}
			this.listenUp(this.editor)
            this.editor.create()
		}
	}
</script>

<style lang="less" scoped="scoped" rel="stylesheet/less">
