<template>
	<div class="articles">
		<div id="writeArticle" v-if="flag">
			<el-form :inline="true" :model="formInline" class="demo-form-inline">
				<el-form-item label="文章标题">
					<el-input v-model="title" placeholder="请输入文章标题" clearable></el-input>
				</el-form-item>
				<el-form-item label="分类" class="select" @click="change">
					<el-select v-model="classifyId" placeholder="请选择文章分类">
						<el-option v-for="item in classify" :label="item.name" :value="item.id" :key="item.id" class="select"></el-option>
					</el-select>
				</el-form-item>
				<el-form-item label="简介" class="textarea">
					<el-input type="textarea" v-model="sintro" @change="cah"></el-input>
				</el-form-item>
				<p style="font-size: 14px; margin-bottom: -5px; color: #666;">缩略图:</p>
				<el-upload :on-success="getpath"  name="uploadImg" class="upload-demo" action="/article/uploadImg" :on-remove="handleRemove" multiple :limit="3" :on-exceed="handleExceed" :file-list="fileList">
					<el-button size="small" type="primary">点击上传</el-button>
					<!-- <div slot="tip" class="el-upload__tip">只能上传jpg/png文件，且不超过500kb</div> -->
				</el-upload>
			</el-form>
			<editor @html="getHtml"></editor>
			<el-button type="primary" @click="addArticle">发表</el-button>
		</div>
		<div class="write" v-if="!flag">
			<h3 class="tip">登录后才能发表文章哦!</h3>
		</div>
	</div>

</template>

<script>
	import editor from './editor.vue'
	export default {
		data() {
			return {
				flag:false,
				html: '',
				title: '',
				classify: null,
				classifyId: '',
				fileList: null,
				path:'/static/img/0e6987f0-47c4-11e9-be07-ab9daf6d4a45.jpeg',
				sintro:''
			}
		},
		methods: {
			getHtml(html) {
				this.html = html
			},
			getpath(response){
				this.path = response.data[0]
				console.log(this.path)
			},
			change() {
				console.log(this.classifyId)
			},
			addArticle() {
				let userId = localStorage.getItem('id') ? localStorage.getItem('id') : 1
				this.$http.post('/article/addArticle', {
					title: this.title,
					content: this.html,
					classifyId: this.classifyId,
					userId: userId,
					path:this.path,
					sintro:this.sintro
				}).then(data => {
					if (data.body.success) {
						let that = this
						this.$alert('文章发表成功！',{
							callback:function(){
								location.reload()
							}
						})
					} else {
						this.$alert('未成功，请稍后再试')
					}
				})
			},
			getClassify() {
				this.$http.get('/classify/queryClassify').then(data => {
					this.classify = data.body.rows
				})
			},
			checkLogin() {
				this.$http.get('/user/check').then(data => {
					if (data.body.login === true) {
						this.flag = true
					}
				})
			},
			handleRemove(response,file, fileList) {
				let path = response.response.data[0]
				this.$http.get('/article/delete',{params:{path:path}}).then(data=>{
					if(data.body.success){
						return
					}else{
						console.log('移除失败')
					}
				})
				
			},
			handleExceed(files, fileList) {
				this.$message.warning(`当前限制选择 3 个文件，本次选择了 ${files.length} 个文件，共选择了 ${files.length + fileList.length} 个文件`);
			}
		},
		components: {
			editor
		},
		created() {
			this.checkLogin()
			this.getClassify()
		}
	}
</script>

<style lang="less" rel="stylesheet/less">
	#writeArticle {
		padding-left: 25px;
		.textarea{
			display: block;
			width: 500px;
			input,textarea{
				width: 460px !important;
				display: block;
			}
		}
		.select {
			z-index: 999 !important;
		}
		.upload-demo{
			margin: 20px 0;
		}
		label {
			display: block;
			font-size: 14px;
			height: 45px;
			line-height: 45px;
			text-align: left;
		}

		.el-select-dropdown__list {
			position: relative !important;
			z-index: 999;
		}

		#editor {
			min-width: 720px;
			max-width: 960px;
			margin-bottom: 25px;
			z-index: 1;
			position: relative;
		}
	}

	.tip {
		font-size: 25px;
		font-weight: 700;
		color: cornflowerblue;
		text-align: center;
		padding-top: 25px;
	}
</style>



