// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import './css/reset.css'
import Vuex from 'vuex'
Vue.use(Vuex)
import VueResource from 'vue-resource'
import './lib/font/iconfont.css'
Vue.use(VueResource)
import moment from 'moment' 
import ElementUI from 'element-ui' 
import 'element-ui/lib/theme-chalk/index.css'
Vue.use(ElementUI)
Vue.http.options.emulateJSON = true
Vue.config.productionTip = false




var store =new Vuex.Store({
	state:{  //可以通过this.$store.state.属性名来获取需要的数据
		articleCount:0
	},
	mutations:{
		updateArticleCount: function (state,data){
			state.articleCount = data
		}
	}
})

function formatFunc(str) {
	return str > 9 ? str : '0' + str
}


Vue.filter('dateFormate',function(dateStr){
	if(!dateStr){
		return '-'
	}
	var date = new Date(dateStr)   
	var year = date.getFullYear()
	var mon = formatFunc(date.getMonth() + 1)
	var day = formatFunc(date.getDate())
	var hour = date.getHours()>9? date.getHours() : '0' + date.getHours()
	var min = date.getMinutes()>9? date.getMinutes() : '0' + date.getMinutes()
	var sec = date.getSeconds()>9? date.getSeconds() : '0' + date.getSeconds()
	var dateStr = year+'-'+mon+'-'+day+' '+hour+':'+min+':'+sec
	return dateStr
})


/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  store,
  components: { App },
  template: '<App/>'
})



