<template>
	<div id="comment">
		<h3><span class="iconfont">&#xe6ad;</span>&nbsp;文章评论</h3>
		<ul class="commentsList">
			<li v-for="item in comments" :key="item.id">
				<div class="com">
					<img :src="item.avator" alt="用户头像">
					<div class="info">
						<p><span class="name">{{item.username}}</span> &nbsp;<span class="time">{{item.ctime | dateFormate}}</span></p>
						<p>{{item.content}}</p>
					</div>
				</div>
				<reply :commentid="item.id"></reply>
			</li>
		</ul>
		<h4 v-if="!comments.length">还没有人评论哦，快来抢沙发吧...</h4>
		<div class="input">
			<el-input type="textarea" placeholder="来都来了,留下评论在走呗!" v-model="texts"></el-input>
		</div>
		<el-button type="primary" @click="addComment">发送</el-button>
	</div>
</template>

<script>
	import reply from './reply.vue'
	export default {
		data() {
			return {
				comments:[],
				page:1,
				texts:''
			}
		},
		components:{
			reply
		},
		methods:{
			getComments(){
				let articleId = this.$route.query.id
				this.$http.get('/comment/queryComment',{params:{articleId:articleId,page:this.page}}).then(data=>{
					this.comments = data.body
				})
			},
			addComment(){
				let articleId = this.$route.query.id;
				let	userId = localStorage.getItem('id') ? localStorage.getItem('id') : 1;
				this.$http.get('/user/queryUser',{params:{id:userId}}).then(data=>{
					if(data.body.message=='未登录'){
						return this.$message({
							message: '登录后才能评论哦',
							type: 'error'
						})
					}
					let username = data.body[0].username;
					let avator = data.body[0].avator;
					if(!this.texts){
						return  this.$message({
							message: '您还没有输入任何内容哦！',
							type: 'error'
						})
					}
					this.$http.post('/comment/addComment',{content:this.texts,articleId:articleId,userId:userId}).then(data=>{
						if(data.body.success){
							this.comments.push({
								id:1,
								username:username,
								avator:avator,
								content: this.texts,
								ctime:Date.now()
							})
							this.$message({
								message: '回复成功',
								type: 'success'
							})
						 }else{
							 this.$message({
							 	message: '评论失败',
							 	type: 'error'
							 }) 
						 }
						
					})
				})
			}
		},
		
		created(){
			this.getComments()
		},
		watch:{
			$route:function(){
				this.getComments()
			}
		}
	}
</script>

<style lang="less" rel="stylesheet/less" scoped="scoped">
	#comment{
		margin-top: 15px;
		padding: 15px;
		background-color: #ddd;
		h3{
			width: 85px;
			height: 40px;
			line-height: 40px;
			font-weight: 700;
			border-bottom: 3px solid cornflowerblue;
			transition: all 0.5s ease;
			margin-bottom: 15px;
			cursor: pointer;
			&:hover{
				width: 105px;
			}
		}
		.input{
			margin-bottom: 15px;
		}
		h4{
			margin-bottom: 15px;
		}
		.commentsList{
			li{
				margin-bottom: 15px;
			}
			img{
				width: 40px;
				height: 40px;
				display: block;
			}
			.com{
				.info{
					width: 880px;
					.time{
						font-size: 14px;
					}
					.name{
						color: cornflowerblue;
					}
				}
				display: flex;
				justify-content: space-between;
			}
		}
	}
</style>



