<template>
	<div class="right">
		<ul class="menu">
			<li v-for="item in recomend" :key="item.id">
				<router-link :to="'/home/article?id='+item.id" :title="item.title">
					<img :src="item.img" :alt="item.title">
					<span>{{item.title}}</span>
				</router-link>
			</li>
		</ul>

		<div class="order">
			<h3><span>收藏排行</span></h3>
			<ul class="list">
				<router-link :to="'/home/article?id='+this.collect[0].id" tag="li">
					<span class="title">{{collect[0].title}}</span>
					<img :src="this.collect[0].img" :alt="this.collect[0].title">
				</router-link>
				<router-link v-if="i>0" v-for="(item,i) in collect" :to="'/home/article?id='+item.id" tag="li" :key="item.id">
					<b>{{i+1}}</b>{{item.title}}
				</router-link>
			</ul>
		</div>
		<div class="order">
			<h3><span>最近更新</span></h3>
			<ul class="upList">
				<router-link v-for="(item,i) in update" :to="'/home/article?id='+item.id" tag="li" :key="item.id">
					{{item.title}}
				</router-link>
			</ul>
		</div>
		<div class="order">
			<h3><span>最新评论</span></h3>
			<ul class="commentList">
				<router-link v-for="(item,i) in comment" :to="'/home/article?id='+item.articleid" tag="li" :key="item.id">
					{{item.content}}
				</router-link>
			</ul>
		</div>
		<div class="order">
			<h3><span>站点统计</span></h3>
			<ul class="tz">
				<li><span class="key">建站时间:</span>2019-03-15</li>
				<li><span class="key">网站程序:</span>Node.js博客管理系统</li>
				<li><span class="key">文章统计:</span>共{{count}}篇文章</li>
				<li><span class="key">所用技术:</span>Node.js+Vue</li>
				<img src="http://localhost:3000/static/img/qrcode.jpg" alt="联系我">
			</ul>
		</div>
	</div>
</template>

<script>
	export default {
		data() {
			return {
				recomend: null,
				collect: null,
				special: null,
				update: null,
				count: 0,
				comment:null
			}
		},
		methods: {
			getCommnet(){
				this.$http.get('/comment/update').then(data=>{
					this.comment = data.body
				})
			},
			getArticleCount(){
				this.$http.get('/article/articleLength').then(data=>{
					let count = data.body[0].count
					this.count = count
					this.$store.commit('updateArticleCount',count)
				})
			},
			getRecoment() {
				this.$http.get('/article/recomend').then(data => {
					this.recomend = data.body
				})
			},
			getcollet() {
				this.$http.get('/article/recomend', {
					params: {
						num: 6
					}
				}).then(data => {
					this.collect = data.body
				})
			},
			getUpdate() {
				this.$http.get('/article/update').then(data => {
					this.update = data.body
				})
			}
		},
		created() {
			this.getArticleCount()
			this.getRecoment()
			this.getcollet()
			this.getUpdate()
			this.getCommnet()
		}
	}
</script>

<style lang="less" rel="stylesheet/less" scoped="scoped">
	.right {
		width: 270px;
		margin-bottom: 30px;
		.commentList{
			li{
				cursor: pointer;
				white-space: nowrap;
				overflow: hidden;
				text-overflow: ellipsis;
				background-color: #eee;
				margin: 10px 0;
				line-height: 30px;
				height: 30px;
			}
		}
		h3 {
			padding-left: 0 !important;
		}

		.tz {
			padding-bottom: 15px;

			img {
				width: 100%;
				height: auto;
				display: block;
			}

			li {
				height: 35px;

				.key {
					font-weight: 700;
					margin-right: 5px;
				}
			}
		}

		.upList {
			li {
				background-color: #ddd;
				margin: 15px 0;
				padding: 10px 0 10px 30px;
				text-overflow: ellipsis;
				overflow: hidden;
				white-space: nowrap;
				position: relative;
				cursor: pointer;

				&:before {
					position: absolute;
					content: "";
					width: 4px;
					height: 4px;
					background: #000;
					border-radius: 50%;
					left: 15px;
					top: 18px;
				}
			}
		}
	}

	.order,
	.special {
		background-color: #fff;
		margin-top: 15px;
		font-size: 16px;
		display: inline-block;
		vertical-align: top;

		h3 {
			height: 45px;
			line-height: 45px;
			padding-left: 25px;

			span {
				display: block;
				font-weight: 700;
				height: 40px;
				width: 65px;
				border-bottom: 3px solid cornflowerblue;
				transition: all 0.5s ease;
				cursor: pointer;

				&:hover {
					width: 80px;
				}
			}
		}
	}

	.order {
		width: 270px;
		padding: 0 15px;

		b {
			display: inline-block;
			width: 20px;
			height: 20px;
			background-color: cornflowerblue;
			text-align: center;
			margin-right: 5px;
			color: #fff;
		}

		>.list li {
			cursor: pointer;
			position: relative;
			margin: 15px 0;
			text-overflow: ellipsis;
			white-space: nowrap;
			overflow: hidden;

			span {
				position: absolute;
				top: 50%;
				right: 15px;
				overflow: hidden;
				transform: translateY(-50%);
				left: 15px;
				color: #fff;
			}

			img {
				display: block;
				width: 100% !important;
			}
		}
	}

	.menu {
		font-size: 16px;
		display: inline-block;
		background-color: rgba(0, 0, 0, .1);
		padding-bottom: 0px;
		li {
			position: relative;
			overflow: hidden;
			&:first-child{
				margin-bottom: 10px;
			}

			&:hover img {
				transform: scale(1.2)
			}

			img {
				width: 100%;
				height: 135px;
				display: block;
				transition: all 0.5s ease;
			}

			span {
				position: absolute;
				padding-left: 15px;
				bottom: 10px;
				color: #fff
			}
		}
	}
</style>



