<template>
	<header id="header">
		<div class="navbox">
			<h2 id="mnavh"><span class="navicon"></span></h2>
			<div class="logo">
				<RouterLink to="/">
					<img src="../lib/uugai.com_1552549749534.png" alt="logo">
				</RouterLink>
			</div>
			<nav>
				<ul id="starlist">
					<li>
						<router-link to="/">首页</router-link>
					</li>
					<li class="menu"><a href="javascript:;">分类浏览</a>
						<ul class="sub">
							<li v-for="item in classify" :key="item.id">
								<router-link :to="'/home/newsList?classifyid='+item.id">{{item.name}}</router-link>
							</li>
						</ul>
						<span></span>
					</li>
					<!-- <li>
						<RouterLink to="/message">留言</RouterLink>
					</li> -->
					<li>
						<RouterLink to="/writeArticle">写文章</RouterLink>
					</li>
					<li class="menu">
						<RouterLink to="/home/user">关于我</RouterLink>
						<ul class="sub userInfo">
							<li class="white avator"><img :src="user.avator" alt="头像"></li>
							<router-link to="/login" v-if="user.username==='未登录'" tag="li" class="white login">点击登录</router-link>
							<li class="white" v-if="user.phone">{{user.username}}<span class="iconfont" v-if="user.sex==1">&#xe606;</span><span class="iconfont men" v-if="user.sex==0">&#xe605;</span></li>
							<li class="white" v-if="user.phone">{{user.phone}}</li>
						</ul>
					</li>
				</ul>
			</nav>
			<div class="search">
				<input type="text" value="" placeholder="搜索文章" v-model="keywords" />&nbsp;
				<button @click="search"><span class="iconfont">&#xe630;搜索</span></button>&nbsp;&nbsp;
				<span class="exit" @click="exit"><span class="iconfont">&#xe64b;</span>退出</span>
			</div>
		</div>
	</header>
</template>

<script>
	export default {
		data() {
			return {
				classify: null,
				keywords:'',
				user: {
					username: '未登录',
					avator: 'http://localhost:3000/static/img/akari.jpg',
					phone:''
				}
			}
		},
		methods: {
			getClassify() {
				this.$http.get('/classify/queryClassify').then(data => {
					this.classify = data.body.rows
				})
			},
			getUser(){
				this.$http.get('/user/check').then(data=>{
					if(data.body.status == 400){
						return console.log('未登录')
					}
					let phone = localStorage.getItem('email')
					this.$http.get('/user/queryUserByPhone',{params:{phone:phone}}).then(data=>{
						if(data.body.message ==="未登录"){
							return console.log('您还未登录!')
						}
						// console.log(data)
						this.user = data.body[0]
					})
				})
			},
			exit() {
				this.$confirm('您确定要退出吗？', '提示', {
					confirmButtonText: '确定',
					cancelButtonText: '取消',
					type: 'warning'
				}).then(() => {
					this.$http.get('/user/exit').then(data => {
						if (data.body.success) {
							this.user = {
								username: '未登录',
								avator: 'http://localhost:3000/static/img/akari.jpg',
								phone:''
							}
							this.$router.push('/')
						} else {
							this.$message({
								type: 'info',
								message: '退出成功'
							})
						}
					})
				}).catch(() => {
					this.$message({
						type: 'info',
						message: '已取消'
					});
				});
			
			},
			search(){
				if(!this.keywords){
					return this.$message('请输入关键字进行搜索')
				}
				this.$router.push('/home/newsList?keywords='+this.keywords)
			}
		},
		watch:{
			$route(){
				this.getUser()
			}
		},
		created() {
			this.getUser()
			this.getClassify()
		}
	}
</script>

<style lang="less" rel="stylesheet/less" scoped="scoped">
	header {
		width: 100%;
		padding: 5px 0;
		background: cornflowerblue;
		position: fixed;
		top: 0;

		.navbox {
			width: 1290px;
			margin: 0 auto;
		}
	}

	#header {
		z-index: 9;
		.search {
			height: 50px;
			overflow: hidden;
			line-height: 50px;
			float: right;
			input,
			button {
				height: 40px;
				line-height: 40px;
				border: none;
				outline: none;
				border-radius: 7px;
			}

			input {
				width: 250px;
				padding-left: 15px;

				&:focus {
					border: 1px solid greenyellow;
				}
			}

			button {
				width: 80px;
				background-color: #67C23A;
				color: #FFFDEF;
			}
		}
		.exit{
			cursor: pointer;
		}
		.userInfo{
			padding: 15px 15px;
			background-color: #FFFDEF !important;
			.white{
				color: cornflowerblue ;
				display: block;
				margin: 5px auto;
				width: 100%;
				text-align: center;
				cursor:pointer;
				.men{
					color: hotpink !important;
				}
			}
			.login{
				color: #fff;
				 background-color: cornflowerblue !important;
			}
			.avator{
				height: 100px;
				img{
					border-radius: 50%;
					margin: 0 auto;
					width: 100px;
					height: 100px;
					display: block;
					// margin: 5px auto;
				}
			}
		}
		
		
	}

	header::before {
		background: #000 linear-gradient(to left, #4cd964, #5ac8fa, #007aff, #34aadc, #5856d6, #ff2d55);
		content: "";
		height: 5px;
		position: absolute;
		top: 0;
		width: 100%;
	}

	.logo {
		float: left;
		margin-right: 60px;
		line-height: 50px;
		color: #FFF;
		font-size: 22px;
		height: 50px;
		width: auto;
	}

	.logo a {
		color: #FFF
	}

	.logo img {
		height: 50px;
		width: auto;
		// margin-top: -5px;
		background-color: cornflowerblue;
	}

	.imgscale img {
		width: 100%;
		-moz-transition: all .5s ease;
		-webkit-transition: all .5s ease;
		-ms-transition: all .5s ease;
		-o-transition: all .5s ease;
		transition: all .5s ease;
		transition: all 0.5s;
	}

	.imgscale:hover img {
		transform: scale(1.1);
	}

	nav {
		float: left;
		height: 50px;
		line-height: 50px;
		text-align: center;
	}

	#starlist {}

	#starlist li {
		position: relative;
		display: inline;
		float: left;
		width: max-content;
	}

	#starlist li a {
		display: inline;
		float: left;
		padding: 0 20px;
		width: 100%;
		display: block;
		color: #fff
	}

	#starlist li a:hover,
	#starlist #selected,
	.selected>a,
	#starlist li:hover {
		color: #00c1de;
	}

	#starlist li:hover a {
		color: #00c1de
	}

	#starlist ul.sub {
		overflow: hidden;
		position: absolute;
		left: 0;
		top: 50px;
		background: cornflowerblue;
		display: none;
		z-index: 9
	}

	#starlist ul.sub li {
		height: 30px;
		line-height: 30px;
		font-size: 14px;
	}

	#starlist ul.sub li:last-child {
		padding-bottom: 10px
	}

	#starlist ul.sub li a {
		color: #FFF
	}

	#starlist ul.sub li a:hover {
		color: #00c1de
	}

	#starlist li:hover ul.sub {
		display: block
	}

	.menu:before {
		position: absolute;
		content: "";
		top: 22px;
		right: 5px;
		background: url(../img/jt.png) no-repeat;
		width: 8px;
		height: 8px;
		-ms-transition: all .5s ease;
		-moz-transition: all .5s ease;
		-webkit-transition: all .5s ease;
		-o-transition: all .5s ease;
		transition: all .5s ease;
	}

	.menu:hover:before {
		transform: rotate(180deg)
	}

	/*旋转*/
	#mnavh {
		display: none;
		margin: 5px 0 0 0;
		width: 24px;
		height: 40px;
		float: right;
		text-align: center;
		padding: 0 10px;
	}

	.navicon {
		display: block;
		position: relative;
		width: 30px;
		height: 5px;
		background-color: #fff;
		margin-top: 20px;
	}

	.navicon:before,
	.navicon:after {
		content: '';
		display: block;
		width: 30px;
		height: 5px;
		position: absolute;
		background: #fff;
		-webkit-transition-property: margin, -webkit-transform;
		transition-property: margin, -webkit-transform;
		transition-property: margin, transform;
		transition-property: margin, transform, -webkit-transform;
		-webkit-transition-duration: 300ms;
		transition-duration: 300ms;
	}

	.navicon:before {
		margin-top: -10px;
	}

	.navicon:after {
		margin-top: 10px;
	}

	.open .navicon {
		background: none
	}

	.open .navicon:before {
		margin-top: 0;
		-webkit-transform: rotate(45deg);
		transform: rotate(45deg);
	}

	.open .navicon:after {
		margin-top: 0;
		-webkit-transform: rotate(-45deg);
		transform: rotate(-45deg);
	}

	.open .navicon:before,
	.open .navicon:after {
		content: '';
		display: block;
		width: 30px;
		height: 5px;
		position: absolute;
		background: #fff;
	}
</style>



