<template>
	<div id="user">
		<img :src="user.avator" alt="">
		<p>{{user.username}}   <span  v-if="user.sex==1" class="iconfont man">&#xe606;男</span><span  v-if="user.sex==0" class="iconfont men">&#xe605;女</span></p>
		<p>欢迎您，来到我的博客，你，我生命中一个重要的过客，我们之所以是过客，因为你未曾会为我停留。</p>
		<!-- <p>{{user.addr}}</p> -->
		<p>{{user.intro}}</p>
		<!-- <a target="_blank" href="http://wpa.qq.com/msgrd?v=3&uin=304338620&site=qq&menu=yes"><img border="0" src="http://wpa.qq.com/pa?p=2:304338620:52" alt="联系我" title="联系我"/></a> -->
		<div class="myComment">
			<h3 class="title">我的文章</h3>
			<p v-for="(item,i) in article" :key="item.id">
				<router-link :to="'/home/article?id='+item.id">{{item.title}}</router-link>
				<span class="delete" @click="delArticle(item.id,i)">删除</span>
			</p>
			<div @click="getMore" class="more">{{tips}}</div>
		</div>
		<div class="myComment">
			<h3 class="title">我的评论</h3>
			<p v-for="(item,i) in comment" :key="item.id">
				<router-link :to="'/home/article?id='+item.articleid">{{item.content}}</router-link>
				<span class="delete" @click="delComment(item.id,i)">删除</span>
			</p>
			
		</div>
	</div>
</template>

<script>
	export default{
		data(){
			return {
				user:null,
				collect:null,
				article:[],
				comment:[],
				id:localStorage.getItem('id'),
				page:1,
				Acount:0,
				tips:'点击加载更多'
			}
		},
		methods:{
			getMore(){
				if(this.page<Math.ceil(this.Acount/10)){
					this.page++;
					this.getArticle()
				}else{
					this.tips = '没有更多数据了'
				}
				
			},
			getcount(){
				this.$http.get('/article/articleLength',{params:{userId:this.id}}).then(data=>{
					this.Acount = data.body[0].count
				})
			},
			getUser(){
				this.$http.get('/user/queryUser',{params:{id:this.id}}).then(data=>{
					this.user = data.body[0]
				})
			},
			getArticle(){
				this.$http.get('/article/queryUser',{params:{userId:this.id,page:this.page}}).then(data=>{
					this.article = this.article.concat(data.body)
				})
			},
			getComment(){
				this.$http.get('/comment/queryUserComment',{params:{userId:this.id}}).then(data=>{
					this.comment = this.comment.concat(data.body)
				})
			},
			delComment(id,i){
				this.$http.get('/comment/deleteComment',{params:{id:id}}).then(data=>{
					if(data.body.success){
						this.article.splice(i,1)
						this.$message('操作成功')
					}
				})
			},
			delArticle(id,i){
				this.$http.get('/article/deleteArticle',{params:{id:id}}).then(data=>{
					if(data.body.success){
						this.article.splice(i,1)
						this.$message('操作成功')
					}
				})
			}
		},
		created(){
			this.getUser()
			this.getComment()
			this.getArticle()
			this.getcount()
		}
	}
</script>

<style lang="less" scoped="scoped" rel="stylesheet/less">
	#user{
		img{
			float: left;
			margin-right: 15px;
		}
		p{
			line-height: 30px;
			margin: 10px 0;
		}
		.man{
			color: cornflowerblue;
		}
		.more{
			// text-align: center;
			height: 35px;
			line-height:35px ;
			cursor: pointer;
		}
		.men{
			color: hotpink;
		}
		.title{
			width: 65px;
			height: 40px;
			line-height: 40px;
			border-bottom: 3px solid cornflowerblue;
			font-weight: 700;
			transition: all 0.5s ease;
			&:hover{
				width: 80px;
			}
		}
		.myComment{
			a{
				height: 30px;
				line-height: 30px;
			}
			p{
				display: flex;
				justify-content: space-between;
				span{
					color: cornflowerblue;
					cursor: pointer;
				}
			}
		}
	}
		
</style>



