import Vue from 'vue'
import Router from 'vue-router'
import Home from '../component/home.vue'
import Login from '../component/login.vue'
import WriteArticle from '../component/writeArticle.vue'
import NewsList from '../component/newsList.vue'
import User from '../component/user.vue'
import Left from '../component/left.vue'
import Article from '../component/article.vue'
// import Editor from '../component/editor.vue'
Vue.use(Router)

export default new Router({
	routes:[
	{path:'/',redirect:'/home',component:Home},
	{path:'/home',redirect:'/home/main',component:Home,children:[
		{path:'/home/main',component:Left},
		{path:'/home/user',component:User},
		{path:'/home/newsList',component:NewsList},
		{path:'/home/article',component:Article},
	]},
	{path:'/login',component:Login},
	{path:'/writeArticle',component:WriteArticle}
//		{path:'/cart',component:Cart},
//		{path:'/user',component:UserContainer},
//		{path:'/setting',component:Setting},
//		{path:'/home/newsList',component:NewsList},
//		{path:'/home/newsinfo/:id',component:NewsInfo},
//		{path:'/home/photolist',component:PhotoList},
//		{path:'/home/photoinfo/:id',component:PhotoInfo},
//		{path:'/home/goodslist/',component:GoodList},
//		{path:'/home/goodsinfo',component:GoodInfo,children:[{path:'/home/goodsinfo/info',component:Info},{path:'/home/goodsinfo/comment',component:comments},{path:'/home/goodsinfo/description',component:description}]},
	],
	linkActiveClass:'active'
})



